import React, { useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";

const About = () => {
  useEffect(() => {
    document.title = "A propos de moi";
  }, []);

  return (
    <Container className="mt-5 d-flex flex-column">
      <Row className="mb-5">
        <Col xs={3} md={4}></Col>
        <Col>
          <h1>A propos de moi</h1>
        </Col>
        <Col xs={2}></Col>
      </Row>
      <Row>
        <Col
          lg={6}
          className="d-flex justify-content-center align-items-center mb-4"
        >
          <div className="profile-picture-container">
            <img
              src="./img/1R6A1166.JPG"
              alt="Moi"
              className="profile-picture rounded"
            />
          </div>
        </Col>
        <Col className="d-flex flex-column justify-content-center">
          <Row>
            <p className="fs-5">
              Ma formation s'inscrit dans la pluridisciplinarit,
              particulirement entre sciences dures et les sciences humaines. En
              explorant les mathmatiques, le traitement du signal et
              l'informatique, j'ai acquis une expertise technique solide.
              Paralllement, ma soif de comprendre et d'aider les autres m'a
              conduit  explorer l'ergonomie, la psychologie et la conception
              centre utilisateur. Aujourd'hui, je suis sur le point de devenir
              un ingnieur cogniticien capable de concevoir des interfaces
              user-friendly, allant des spcifications  la livraison.
            </p>
          </Row>
          <Row>
            <p className="fs-5">
              Ce qui me passionne le plus dans l'ingnierie cognitive, c'est la
              manire dont il nous permet de transformer la technologie en une
              force d'impact positif. J'ai eu le privilge de travailler avec un
              hpital afin d'amliorer l'exprience numrique des patients par
              exemple. Mon parcours inclut des projets scolaires tout aussi
              enrichissants : de la conception d'une bote aux lettres connecte
               des explorations approfondies sur les montres connectes, chaque
              exprience a renforc ma conviction que les utilisateurs sont au
              cur de chaque solution russie.
            </p>
          </Row>
          <Row>
            <p className="fs-5">
              En dehors de mes aventures professionnelles, j'ai t galement
              investi en tant que vice-trsorier dans une association, ce qui
              m'a permis de dvelopper des comptences en gestion vnementielle
              et budgtaire. Mon amour pour la musique m'a conduit  tre
              ingnieur du son pour les concerts de l'cole, o je collabore
              avec diffrentes personnes pour crer des moments inoubliables.
            </p>
          </Row>
          <Row>
            <p className="fs-5">
              Mon horizon s'tend bien au-del de mes comptences actuelles. Je
              crois en la fusion harmonieuse entre la technologie et l'humain,
              en particulier dans le domaine de la sant. Je suis convaincu que
              l'UX design est la cl pour rendre la technologie accessible 
              tous. Mon objectif est de faire rayonner cette conviction 
              travers mes projets.
            </p>
          </Row>
        </Col>
      </Row>

      <Row className="m-3"></Row>
    </Container>
  );
};

export default About;
